import {Component, ElementRef, Input} from '@angular/core';
import {BadgeVariantType} from './badge.model';
import {
  AdaptRadarAngularService,
  AdaptRadarAngularGenericDirective,
  AdaptRadarAngularCustomEventName
} from '../common/radar/adapt-radar';

@Component({
  selector: 'adapt-badge',
  template: `
    <div tabindex="0"
         [attr.role]="animate ? 'alert' : 'none'"
         [class.badge-animation]="animate"
         [classList]="'badge-pulse' + ' ' + customCls">
      <div [classList]="'hop hop-' + variant"
           class="hop hop-danger">
        <div [classList]="'badge badge-' + variant">
          <div *ngIf="showAlert" [classList]="'badge-dot badge-dot-' + alertVariant">&nbsp;</div>
          <ng-content></ng-content>
        </div>
      </div>
    </div>`
})
export class AdaptBadgeComponent extends AdaptRadarAngularGenericDirective {
  /**
   * Enables pulse animation
   * @docs-default false
   */
  @Input() animate: boolean = false;

  /**
   * Show/hide alert on badge
   * @since v10.1.0
   * @docs-default false
   */
  @Input() showAlert: boolean = false;

  /**
   * Type of badge: primary, secondary, info, danger, warning, success
   * @docs-default primary
   */
  @Input() variant: BadgeVariantType = 'primary';

  /**
   * Type of alert: primary, secondary, info, danger, warning, success
   * @since v10.1.0
   * @docs-default info
   */
  @Input() alertVariant: BadgeVariantType = 'info';

  /**
   * Set custom class for the badge
   * @docs-default
   */
  @Input() customCls: string = '';

  constructor(_adaptRadarService: AdaptRadarAngularService,
              _elem: ElementRef<HTMLElement>) {
    super(_adaptRadarService, _elem, AdaptRadarAngularCustomEventName.AdaptBadge);
  }
}
